import { MOVEMENTS_TYPE_ENUM } from './../../../core/constants/origen-apunte.constant';
import { Component, OnInit, Input, Inject } from '@angular/core';
import { MOVEMENTS_TYPE } from '@core/constants/origen-apunte.constant';
import { PrivateMovementsListaApuntesCliente } from '@api/models/private-movements-lista-apuntes-cliente';

@Component({
  selector: 'app-transaction-item',
  templateUrl: './transaction-item.component.html',
  styleUrls: ['./transaction-item.component.scss'],
})
export class TransactionItemComponent implements OnInit {
  isOpen = false;
  @Input() movement: PrivateMovementsListaApuntesCliente;

  constructor(
    @Inject(MOVEMENTS_TYPE) public movementsType: Array<{ code: string; name: string }>

  ) {}

  ngOnInit(): void {}

  open() {
    this.isOpen = !this.isOpen;
  }

  get description(): string {
    const origenApunte = this.movementsType.find((oa) => oa.code === this.movement.codigoOrigenApunte);
    return origenApunte?.name;
  }

  /*
   * Iconos por tipo de transaccion:
   * Ingreso a depósito (IP): ico-euro
   * Intereses (??): ico-percent
   * Penalizaciones (??): ico-lightning
   * Tranferecnias (TF o TR): ico-transfer
   * Liquidacion de depósitos (HL): ico-refund
   * Otros: ico-bookmark
   */

  get getIcon(): string {
    let icon = null;
    switch (this.movement.codigoOrigenApunte) {
      case MOVEMENTS_TYPE_ENUM.ACUERDO:
      case MOVEMENTS_TYPE_ENUM.APORTACION:
      case MOVEMENTS_TYPE_ENUM.CHEQUE_PAGARE:
      case MOVEMENTS_TYPE_ENUM.CHEQUE_PROPIO:
      case MOVEMENTS_TYPE_ENUM.CHEQUE_EXTRANJERO:
      case MOVEMENTS_TYPE_ENUM.EFECTO_COMERCIAL:
      case MOVEMENTS_TYPE_ENUM.EFECTO_RECIBIDO:
      case MOVEMENTS_TYPE_ENUM.EMBARGO:
      case MOVEMENTS_TYPE_ENUM.IMPOSICION:
      case MOVEMENTS_TYPE_ENUM.RECIBO_DOMICILIADO_RZ:
      case MOVEMENTS_TYPE_ENUM.RECIBO_DOMICILIADO_RB:
      case MOVEMENTS_TYPE_ENUM.RECIBO_VENTANILLA:
      case MOVEMENTS_TYPE_ENUM.REMESA:
      case MOVEMENTS_TYPE_ENUM.RETRO_PENSIONES:
      case MOVEMENTS_TYPE_ENUM.SUBVENCION:
        icon = 'ico-euro';
        break;

      case MOVEMENTS_TYPE_ENUM.LIQUIDACION:
        icon = 'ico-refund';
        break;

      case MOVEMENTS_TYPE_ENUM.TRANSFERENCIA_SEPA_TR:
      case MOVEMENTS_TYPE_ENUM.TRASPASO:
        icon = 'ico-transfer';
        break;

      case MOVEMENTS_TYPE_ENUM.CARG_COMIS_C57_GRP:
        icon = 'ico-percent';
        break;

      case MOVEMENTS_TYPE_ENUM.COMIS_TRASP_VAL:
        icon = 'ico-lightning';
        break;

      default:
        icon = 'ico-bookmark';
        break;
    }
    return icon;
  }

  get isPositive() {
    return this.movement.signo === 'H' ? true : false;
  }
}
