import { ErrorHandlerService } from '@core/services/errors/error-handler.service';
import { CasePayload } from './../../../api/models/case-payload.model';
import { Injectable } from '@angular/core';
import { SalesforceResponse, OtpOutput, ApiError } from '@api/models';
import { environment } from '@environment/environment';
import { HttpClient } from '@angular/common/http';
import { PrivateMovementsData } from '@api/models/private-movements-data';
import { CurrentSearch } from '@api/models/moviments-serach';
import { pluck, catchError } from 'rxjs/operators';
import { OtpMoviments } from '@core/models/otp-movimentes';
import { ApiErrors } from '@api/constants';
import { of } from 'rxjs';

@Injectable({
  providedIn: 'root',
})
export class TransactionService {
  constructor(private httpClient: HttpClient, private errorHandlerService: ErrorHandlerService) {}

  getTransaction(
    acuerdo: string,
    dateFromParam: string,
    dateToParam: string,
    referenceOTP?: string
  ) {
    // tslint:disable-next-line: max-line-length
    let url = `${environment.apiBasePath}${environment.endpoints.trasactions.get}?acuerdo=${acuerdo}&fechaDesde=${dateFromParam}&fechaHasta=${dateToParam}`;
    if (referenceOTP) {
      url = `${url}&referenceOTP=${referenceOTP}`;
    }
    return this.httpClient.get<SalesforceResponse<PrivateMovementsData>>(url).pipe(
      catchError((error: ApiError) => {
        if (error.errorCode === ApiErrors.MOV_NOT_FOUND) {
          return of({ code: ApiErrors.MOV_NOT_FOUND, data: [] });
        }
        return this.errorHandlerService.redirectErrorPage(error);
      })
    );
  }

  generateOtp(body: OtpMoviments) {
    const url = `${environment.apiBasePath}${environment.endpoints.trasactions.generateOtp}`;
    return this.httpClient.post<any>(url, body);
  }

  validateOtp(body: OtpMoviments) {
    const url = `${environment.apiBasePath}${environment.endpoints.trasactions.validateOtp}`;
    return this.httpClient.put<any>(url, body);
  }
}
