import { format } from 'util';
import { TransactionService } from '@core/services/transactions/transaction.service';
import { FormGroup, FormBuilder, Validators, AbstractControl } from '@angular/forms';
import { Component, OnInit, ViewChild, TemplateRef, OnDestroy, Inject } from '@angular/core';
import { DatepickerDateCustomClasses, BsLocaleService } from 'ngx-bootstrap/datepicker';
import * as moment from 'moment';
import { BsModalRef, BsModalService } from 'ngx-bootstrap/modal';
import { ErrorHandlerService } from '@core/services/errors/error-handler.service';
import { Subscription, zip } from 'rxjs';
import { CurrentSearch } from '@api/models/moviments-serach';
import { formatDate } from '@angular/common';
import { GlobalPositionService } from '@core/services/global-position/global-position.service';
import { GlobalPosition, FinancialAccount } from '@api/models';
import { ProductTypes } from '@core/constants';
import { map, switchMap, finalize } from 'rxjs/operators';
import { PrivateMovementsData } from '@api/models/private-movements-data';
import { findAccount, findDeposit, isInvalidOtp } from '@core/utils';
import { PrivateMovementsListaApuntesCliente } from '@api/models/private-movements-lista-apuntes-cliente';
import { ApiErrors } from '@api/constants';
import { Banners, BANNERS } from '@core/constants/banners.constants';

@Component({
  selector: 'app-transaction-search',
  templateUrl: './transaction-search.component.html',
  styleUrls: ['./transaction-search.component.scss'],
})
export class TransactionSearchComponent implements OnInit, OnDestroy {
  @ViewChild('otpModal') otpModal: TemplateRef<any>;
  private subs = new Subscription();
  state$ = zip(
    this.globalPositionService.globalPosition$,
    this.globalPositionService.globalPosition$.pipe(findAccount()),
    this.globalPositionService.globalPosition$.pipe(findDeposit())
  ).pipe(map(([globalPosition, account, deposit]) => ({ globalPosition, account, deposit })));

  acuerdo: string;
  IBAN: string;
  searchForm: FormGroup;
  movementsSearchList: Array<PrivateMovementsListaApuntesCliente>;
  maxDateTo = new Date();
  minDateFrom = new Date(moment().subtract(365, 'days').format('L'));
  dateCustomClasses: DatepickerDateCustomClasses[];
  dateFrom: Date;
  dateTo: Date;
  formSubmit = false;
  private otpId: string;
  private modalRef: BsModalRef;
  invalidOtp = false;
  currentSearch: CurrentSearch;
  saveAccount: FinancialAccount;
  showMsgTableEmpty: boolean;
  invalidMovimentsOtpCode: boolean;

  constructor(
    private formBuilder: FormBuilder,
    private globalPositionService: GlobalPositionService,
    private bsLocaleService: BsLocaleService,
    private errorHandler: ErrorHandlerService,
    private modalService: BsModalService,
    private transactionService: TransactionService,
    @Inject(BANNERS) public readonly banners: Banners
  ) {
    moment.locale('es');
    const now = new Date();
    this.dateCustomClasses = [{ date: now, classes: [] }];
  }

  ngOnInit(): void {
    this.bsLocaleService.use('es');
    this.createForm();
  }

  get maxDateFrom() {
    return this.f.dateTo.value ? new Date(this.f.dateTo.value) : new Date();
  }

  get minDateTo() {
    return this.f.dateFrom.value
      ? new Date(this.f.dateFrom.value)
      : new Date(moment().subtract(365, 'days').format('L'));
  }

  private createForm() {
    const today = new Date();
    const lastMonth = new Date(
      `${today.getMonth() > 0 ? today.getMonth() : 1}/${today.getDay()}/${today.getFullYear()}`
    );

    this.searchForm = this.formBuilder.group({
      dateFrom: [lastMonth, [Validators.required]],
      dateTo: [today, [Validators.required]],
    });
  }

  get f() {
    return this.searchForm.controls;
  }

  search() {
    this.formSubmit = true;
    if (this.searchForm.valid) {
      this.currentSearch = {
        acuerdo: this.acuerdo,
        fechaDesde: this.formatCustomDate(this.f.dateFrom.value),
        fechaHasta: this.formatCustomDate(this.f.dateTo.value),
      };
      this.makeSearch();
    }
  }

  openModal(ref: TemplateRef<any>) {
    if (!this.modalRef || (this.modalRef && !this.modalRef.content)) {
      this.modalRef = this.modalService.show(ref);
    }
  }

  formatCustomDate(date: Date, customFormat?: string) {
    return formatDate(date, customFormat || 'yyyy-MM-dd', 'es');
  }

  generateOtp(openModal?: boolean) {
    this.invalidMovimentsOtpCode = false;
    this.subs.add(
      this.state$
        .pipe(
          switchMap((state) =>
            this.transactionService.generateOtp({
              iban: state.account.IBAN,
              dateFrom: this.currentSearch.fechaDesde,
              dateTo: this.currentSearch.fechaHasta,
            })
          )
        )
        .subscribe(
          ({ otpId }) => {
            this.otpId = otpId;
            if (openModal) {
              this.openModal(this.otpModal);
            }
          },
          (error) => {
            this.errorHandler.showError(error);
          }
        )
    );
  }

  validateOtp(secretKey: string) {
    this.subs.add(
      this.state$
        .pipe(
          switchMap((state) =>
            this.transactionService.validateOtp({
              iban: state.account.IBAN,
              dateFrom: this.currentSearch.fechaDesde,
              dateTo: this.currentSearch.fechaHasta,
              otpId: this.otpId,
              secretKey,
            })
          )
        )
        .subscribe(
          ({ code }) => {
            this.makeSearch(this.otpId);
            this.closeModal();
          },
          (error) => {
            if (isInvalidOtp(error)) {
              this.invalidMovimentsOtpCode = true;
              return;
            }
            this.errorHandler.showError(error);
            this.closeModal();
          }
        )
    );
  }

  makeSearch(referenceOTP?: string) {
    this.movementsSearchList = [];
    this.subs.add(
      this.state$
        .pipe(
          switchMap((state) =>
            this.transactionService.getTransaction(
              state.account.Acuerdo,
              this.currentSearch.fechaDesde,
              this.currentSearch.fechaHasta,
              referenceOTP
            )
          )
        )
        .subscribe(
          (result) => {
            if (result) {
              switch (result.code) {
                case 'OK':
                case ApiErrors.MOV_NOT_FOUND:
                  this.movementsSearchList = result ? result.data : [];
                  this.showMsgTableEmpty = true;
                  break;
                case 'FECHA_NO_VALIDA_REQUIERE_OTP':
                  this.generateOtp(true);
                  break;
                case 'NO_EXISTE_OTP':
                  this.movementsSearchList = [];
                  this.errorHandler.showError({ errorCode: result.code });
                  break;

                default:
                  this.movementsSearchList = [];
                  break;
              }
            }
          },
          (error) => {
            this.errorHandler.showError(error);
          }
        )
    );
  }

  trackByDates(item: { [key: string]: PrivateMovementsListaApuntesCliente }) {
    return item.date;
  }

  closeModal() {
    this.modalRef.hide();
  }

  hasError(control: AbstractControl) {
    return this.formSubmit && control.invalid;
  }

  ngOnDestroy() {
    this.globalPositionService.destroy();
    this.subs.unsubscribe();
  }
}
