import { PrivateMovementsData } from '@api/models/private-movements-data';
import { FinancialAccount } from '@api/models';
import { GlobalPositionService } from '@core/services/global-position/global-position.service';
import { Router } from '@angular/router';
import { Component, OnInit, OnDestroy, Inject } from '@angular/core';
import { HeaderService } from '@core/services/header/header.service';
import { TransactionService } from '@core/services/transactions/transaction.service';
import * as moment from 'moment';
import { ErrorHandlerService } from '@core/services/errors/error-handler.service';
import { zip, Subscription } from 'rxjs';
import { map, switchMap, filter, retry } from 'rxjs/operators';
import { findAccount, findDeposit } from '@core/utils';
import { PrivateMovementsListaApuntesCliente } from '@api/models/private-movements-lista-apuntes-cliente';
import { Banners, BANNERS } from '@core/constants/banners.constants';

@Component({
  selector: 'app-transactions',
  templateUrl: './transactions.component.html',
  styles: [],
})
export class TransactionsComponent implements OnInit, OnDestroy {
  readonly rangeDays = 89;
  readonly state$ = zip(
    this.globalPositionService.globalPosition$,
    this.globalPositionService.globalPosition$.pipe(findAccount()),
    this.globalPositionService.globalPosition$.pipe(findDeposit())
  ).pipe(map(([globalPosition, account, deposit]) => ({ globalPosition, account, deposit })));

  movementsList: Array<PrivateMovementsListaApuntesCliente> = [];
  acuerdo: string;
  saveAccount: FinancialAccount;
  showMsgTableEmpty: boolean;

  private subs = new Subscription();

  constructor(
    private headerService: HeaderService,
    private globalPositionService: GlobalPositionService,
    private router: Router,
    private transactionService: TransactionService,
    private errorHandler: ErrorHandlerService,
    @Inject(BANNERS) public readonly banners: Banners
  ) {}

  ngOnInit(): void {
    this.headerService.updateTitle('menu_accounts_web');
    this.globalPositionService.fetchGlobalPosition();

    const dateFrom = moment().subtract(this.rangeDays, 'days').format('YYYY-MM-DD');
    const dateTo = moment().format('YYYY-MM-DD');

    this.subs.add(
      this.state$
        .pipe(
          filter((state) => !!state?.account),
          switchMap((state) =>
            this.transactionService.getTransaction(state?.account.Acuerdo, dateFrom, dateTo)
          )
        )
        .subscribe(
          (result) => {
            this.movementsList = result?.data;
            this.showMsgTableEmpty = true;
          },
          (error) => {
            this.errorHandler.showError(error);
          }
        )
    );
  }

  openSearch() {
    this.router.navigate(['/search']);
  }

  trackByDates(item: { [key: string]: PrivateMovementsListaApuntesCliente }) {
    return item.date;
  }

  ngOnDestroy() {
    this.globalPositionService.destroy();
    this.subs.unsubscribe();
  }
}
